@font-face {
    font-family: "digital-clock-font";
    src: url("../digital-7.ttf")
}
#timer {
    border: 6px solid;
    border-radius: 40px;
    border-color: rgb(0, 51, 51);
    margin: 25px auto auto auto;
    width: 45%;
    #timer-label {
        font-size: 28px;
        text-align: center;
        margin-top: 20px;
    }
    #time-left {
        font-size: 70px;
        font-family: "digital-clock-font";
        margin-top: -25px;
        margin-bottom: 15px;
        text-align: center;
    }
}