#break-session-container {
  display: flex;
  justify-content: space-between;
  margin-top: -10px;
  @each $element in break, session {
    ##{$element}-label {
      font-size: 28px;
      margin-top: -10px;
    }
    ##{$element}-length {
      font-size: 28px;
    }
    ##{$element}-controller {
      text-align: center;
      margin-top: -30px;
    }
    button {
      border: none;
      background: none;
      color: white;
      outline: none;
      cursor: pointer;
    }
  }
}
