@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@900&display=swap);
#clock{margin:100px auto auto auto;width:550px}#clock #clock-label{font-size:45px;margin-top:-15px;text-align:center}

#break-session-container{display:flex;justify-content:space-between;margin-top:-10px}#break-session-container #break-label{font-size:28px;margin-top:-10px}#break-session-container #break-length{font-size:28px}#break-session-container #break-controller{text-align:center;margin-top:-30px}#break-session-container button{border:none;background:none;color:white;outline:none;cursor:pointer}#break-session-container #session-label{font-size:28px;margin-top:-10px}#break-session-container #session-length{font-size:28px}#break-session-container #session-controller{text-align:center;margin-top:-30px}#break-session-container button{border:none;background:none;color:white;outline:none;cursor:pointer}

@font-face{font-family:"digital-clock-font";src:url(/JavaScriptCaculator/static/media/digital-7.1e670d88.ttf)}#timer{border:6px solid;border-radius:40px;border-color:#033;margin:25px auto auto auto;width:45%}#timer #timer-label{font-size:28px;text-align:center;margin-top:20px}#timer #time-left{font-size:70px;font-family:"digital-clock-font";margin-top:-25px;margin-bottom:15px;text-align:center}

#clock-controller{text-align:center;margin-top:20px}#clock-controller button{border:none;background:none;color:white;outline:none;cursor:pointer}#clock-controller #reset{margin-left:10px}

body{font-family:'Montserrat Alternates', sans-serif;background-color:#066;color:white}

